import { useState } from "react";
import imgs from "./imgsList";
function ImageOnScreen(props) {
  const [counter, setCounter] = useState(0);
 
 
  const mainImgStyle = {
    border: "1px solid saddlebrown",
    opacity: "0.5",
  };

  const images = imgs.map((l, index) => {
    if (index === counter) {
      return (
        <img
          style={mainImgStyle}
          src={l.product}
          key={index}
          id={index}
          onClick={(e) => setCounter(parseInt(e.target.id))}
        />
      );
    } else {
      return (
        <img
          src={l.product}
          id={index}
          key={index}
          onClick={(e) => setCounter(parseInt(e.target.id))}
        />
      );
    }
  });
  const height = window.innerHeight;

  return (
    <div
      className="image-on-whole-screen"
      style={{
        height: height,
      }}
    >
      <svg
        onClick={props.changeScreenView}
        style={{ cursor: "pointer" }}
        className="svg"
        width="14"
        height="15"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="m11.596.782 2.122 2.122L9.12 7.499l4.597 4.597-2.122 2.122L7 9.62l-4.595 4.597-2.122-2.122L4.878 7.5.282 2.904 2.404.782l4.595 4.596L11.596.782Z"
          fill="#fff"
          fillRule="evenodd"
        />
      </svg>
      <div className="image-pleace">
        <button
          name="move-back"
          className="move-picture"
          onClick={() => {
            counter === 0
              ? setCounter(imgs.length - 1)
              : setCounter(counter - 1);
          }}
        >
          <svg
            width="12"
            height="18"
            id="move-back"
            onClick={() => {
              counter === 0
                ? setCounter(imgs.length - 1)
                : setCounter(counter - 1);
            }}
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              id="move-back"
              d="M11  3 3 9l8 8"
              stroke="#1D2026"
              strokeWidth="3"
              fill="none"
              fillRule="evenodd"
            />
          </svg>
        </button>
        <img src={imgs[counter].product} alt="image" />
        <button
          name="move-forward"
          className="move-picture"
          onClick={() => {
            counter > imgs.length - 2 ? setCounter(0) : setCounter(counter + 1);
          }}
        >
          <svg
            width="13"
            height="18"
            xmlns="http://www.w3.org/2000/svg"
            id="move-forward"
            onClick={() => {
              counter > imgs.length - 2
                ? setCounter(0)
                : setCounter(counter + 1);
            }}
          >
            <path
              id="move-forward"
              d="m2 2 8 8-8 8"
              stroke="#1D2026"
              strokeWidth="3"
              fill="none"
              fillRule="evenodd"
            />
          </svg>
        </button>
      </div>
      <div className="flexed-imgs">{images}</div>
    </div>
  );
}

export default ImageOnScreen;
